import $ from "properjs-hobo";
import viewSearch from "../../views/search";
import viewSearchResults from "../../views/search-results";
import Store from "../../core/Store";
import debounce from "properjs-debounce";



class Search {
    constructor ( element, data ) {
        this.element = element;
        this.elemData = data;
        this.data = {};

        this.load().then(() => {
            this.bind();

            if ( this.elemData.results ) {
                this.bindResults();
            }
        });
    }


    load () {
        return new Promise(( resolve ) => {
            this.element[ 0 ].innerHTML = viewSearch();
            this.search = this.element.find( ".js-search-field" );
            this.button = this.element.find( ".js-search-btn" );
            resolve();
        });
    }


    bind () {
        this.button.on( "click", () => {
            this.clear();
        });

        this.search.on( "keyup", () => {
            if ( this.search[ 0 ].value ) {
                this.button.addClass( "is-active" );

            } else {
                this.button.removeClass( "is-active" );
            }
        });
    }


    bindResults () {
        this.results = $( this.elemData.results );
        this.loader = $( this.elemData.loader );
        this.waiting = 300;
        this.isFetch = false;

        this.search.on( "keyup", debounce(() => {
            if ( !this.isFetch && this.search[ 0 ].value ) {
                this.fetch();
            }

        }, this.waiting ));
    }


    emptyResults () {
        this.results[ 0 ].innerHTML = "";
    }


    displayResults ( json ) {
        this.results[ 0 ].innerHTML = viewSearchResults( json );
    }


    clear () {
        this.search[ 0 ].value = "";
        this.search[ 0 ].focus();
        this.button.removeClass( "is-active" );
    }


    fetchQuery ( query ) {
        return $.ajax({
            url: "/api/search/GeneralSearch",
            method: "GET",
            headers: {
                "Content-Type": "application/json;charset=UTF-8"
            },
            dataType: "json",
            data: {
                crumb: Store.crumb,
                q: query,
                p: 0,
                size: 10,
                f_collectionId:	"5caf91b7a4222f77241028dc"
            }
        });
    }


    handle ( response ) {
        this.isFetch = false;
        this.loader.removeClass( "is-active" );

        if ( response.serviceError ) {
            this.displayResults( null );

        } else {
            this.displayResults( response );
        }
    }


    fetch () {
        this.isFetch = true;
        this.loader.addClass( "is-active" );
        this.emptyResults();
        this.fetchQuery( this.search[ 0 ].value ).then(( response ) => {
            this.handle( response );

        }).catch(( response ) => {
            this.handle( response );
        });
    }


    destroy () {}
}



/******************************************************************************
 * Export
*******************************************************************************/
export default Search;
